'use client';

import React, { useState, useEffect } from 'react';
import DataTable from 'react-data-table-component';

const Kpis = () => {
  const [kpis, setKpis] = useState<any[]>([]);

  useEffect(() => {
    const fetchKpis = async () => {
      const res = await fetch('http://localhost:3001/api/kpis');
      const data = await res.json();
      setKpis(data);
    };

    fetchKpis();
  }, []);

  const formatDateTime = (dateStr: string) => {
    const date = new Date(dateStr);
    return date.toLocaleString('fr-FR', {
      weekday: 'short',
      year: 'numeric',
      month: 'long',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  const truncateMiddle = (text: string, maxLength = 40) => {
    if (!text || text.length <= maxLength) return text;
    const half = Math.floor(maxLength / 2);
    return `${text.slice(0, half)}...${text.slice(-half)}`;
  };

  const columns = [
    {
      name: 'Agence',
      selector: (row: any) => row.agence,
      sortable: true,
    },
    {
      name: 'Prestation',
      selector: (row: any) => truncateMiddle(row.prestation),
      sortable: true,
      wrap: true,
      cell: (row: any) => (
        <div title={row.prestation}>
          {truncateMiddle(row.prestation)}
        </div>
      ),
    },
    {
      name: 'Date',
      selector: (row: any) => row.date,
      sortable: true,
      cell: (row: any) => formatDateTime(row.date),
    },
    {
      name: 'Nombre',
      selector: (row: any) => row.count,
      sortable: true,
      right: true,
    },
  ];

  return (
    <div style={styles.container}>
      <h2 style={styles.title}>Statistiques des Prestations</h2>

      <DataTable
        columns={columns}
        data={kpis}
        pagination
        highlightOnHover
        responsive
        striped
        defaultSortFieldId={3}
      />
    </div>
  );
};

const styles: { [key: string]: React.CSSProperties } = {
  container: {
    margin: '40px auto',
    padding: '30px',
    maxWidth: '1000px',
    backgroundColor: '#f9f9f9',
    borderRadius: '10px',
    boxShadow: '0 3px 15px rgba(0,0,0,0.1)',
    fontFamily: 'Segoe UI, sans-serif',
    color: '#344767',
  },
  title: {
    textAlign: 'center',
    color: '#007bff',
    marginBottom: '20px',
  },
};

export default Kpis;